@import url(https://rsms.me/inter/inter.css);
*,
*::after,
*::before {
  box-sizing: border-box;
}
html {
  font-family: 'Inter', sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter var', sans-serif;
  }
}

.app {
  max-width: 1010px;
  margin-left: auto;
  margin-right: auto;
  background-color: #ffffff;
  padding-top: 32px;
  padding-bottom: 78px;
  font-family: 'Inter', serif;
  font-size: 12px;
  position: relative;
}

.pagination {
  text-align: center;
  position: absolute;
  bottom: 18px;
  left: 50%;
  transform: translateX(-50%);
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.movies-list {
  display: grid;
  grid-template-columns: 454px 454px;
  grid-column-gap: 36px;
  -webkit-column-gap: 36px;
          column-gap: 36px;
  grid-row-gap: 32px;
  row-gap: 32px;
  padding: 0;
}

.movie-list__item {
  max-width: 454px;
  max-height: 281px;
  box-shadow: 0 0 20px -10px;
}

.spin_scale {
  transform: scale(2);
  display: block;
  margin-top: 100px;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.movie-card {
  display: flex;
  flex-direction: row;
  position: relative;
  height: 281px;
  width: 454px;
  overflow: hidden;
}

.movie-card__poster {
  height: 100%;
  width: 183px;
  object-fit: cover;
}

.movie-card__information {
  padding: 12px 20px;
}

.movie-card__title {
  margin-top: 0px;
  margin-bottom: 7px;
  max-width: 200px;
  font-size: 20px;
  line-height: 28px;
}

.movie-card__raiting {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  position: absolute;
  width: 30px;
  height: 30px;
  top: 12px;
  right: 9px;
  border: 2px solid #e9d100;
  border-radius: 50%;
}

.movie-card__raiting > p {
  margin: auto;
}

.movie-card__date {
  color: #827e7e;
  margin-top: 0px;
  margin-bottom: 9px;
}

.mivie-card__categorys-list {
  display: flex;
  flex-direction: row;
  padding: 0px;
  margin-bottom: 7px;
}

.movie-card__category {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa;
  margin-right: 8px;
  line-height: 14.52px;
  height: 20px;
  min-width: 46px;
  padding: 2px 4px 3px 5px;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
}

.movie-card__overview {
  margin: 0px;
  line-height: 22px;
}

